body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#loading-screen {
  position: fixed;
  /* display: none; */
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 1);
  z-index: 3;
}

#cover {
  position: fixed;
  /* display: none; */
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.97);
  z-index: 2;

  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  font-size: 2vmin;
  color: #fff;
  text-align: center;
}

#cover-container {
  height: 80%;
  padding: 5% 5%;
  z-index: 3;
  overflow: visible;
  text-align: center;
}

.cover-photo img {
  text-align: center;
  height: 30vmin;
  /* border: 2px solid darkslategray; */
  border-radius: 100%;
  /* box-shadow: 2px 2px gray; */
}

.cover-title {
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 8vmin;
  letter-spacing: 0.34em;
  margin: 25px auto;

  background: -webkit-linear-gradient(white, #38495a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.cover-text {
  text-align: right;
  letter-spacing: 0.1em;
  padding-left: 25%;
}

#button-container {
  height: 20%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#bootstrap-overrides-btn {
  border-radius: 0;
}

#navbar {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 5%;
  min-width: 40px;
  color: white;

  height: 400px;
  margin: auto;

  text-align: center;
}

.icon {
  display: block;
  height: 39px;
  margin: calc((400px - (4 * 39px)) / (4 + 1)) 0;

  cursor: pointer;

  transition: all 0.3s ease-out 0.1s;
  -webkit-transition: all 0.3s ease-out 0.1s;
  -moz-transition: all 0.3s ease-out 0.1s;
  -o-transition: all 0.3s ease-out 0.1s;
  -ms-transition: all 0.3s ease-out 0.1s;
}

i {
  font-size: 20px;
}

.icon > .tag {
  font-size: 10px;
  margin: 5px 0;
}

.icon:hover {
  opacity: 0.5;
}

.selected {
  transform: scale(1.5);
  -webkit-transform: scale(1.5);
  -moz-transform: scale(1.5);
  -o-transform: scale(1.5);
  -ms-transform: scale(1.5);

  transition: all 0.3s;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -o-transition: all 0.3s;
  -ms-transition: all 0.3s;

  /* color: gold; */
  color: rgb(33, 150, 243);
}

#content {
  width: 95%;
  max-width: calc(100% - 40px);
  height: 100%;
  background: white;

  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  -ms-transition: all 0.5s;
}

#about {
  width: 100%;
  height: 100%;
  overflow: auto;
  text-align: none;
  background: lightgray;
}

#about::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
  background-color: #444444;
}

#about::-webkit-scrollbar {
  width: 6px;
  background-color: #f5f5f5;
}

#about::-webkit-scrollbar-thumb {
  /* background-color: gold; */
  background-color: rgb(33, 150, 243);
  border: 1px solid #000000;
}

.vertical-timeline-element-icon {
  padding: 18px !important;
}

@media only screen and (max-width: 1170px) {
  .material-icons {
    padding: 0px !important;
  }
  .vertical-timeline-element-icon {
    padding: 8px !important;
  }
}

.content-italic {
  font-style: italic;
}

#skills {
  width: 100%;
  height: 100%;
  z-index: 10;
}

#works {
  width: 100%;
  height: 100%;
  /* background: lightgray; */
}

#stage {
  display: -webkit-inline-flex;
  display: inline-flex;
  width: 70%;
  height: 100%;
  background: #444444;
}

#panel {
  position: absolute;
  display: inline-block;
  width: 30%;
  height: 100%;
  background: lightgray;
  box-shadow: 0px 0px 20px #222222;
}

#stage-video {
  width: 100%;
}

#header {
  display: table;
  width: 100%;
  height: 60px;
  text-align: center;
  /* margin-top: 10px; */
}

#left-button,
#right-button {
  display: table-cell;
  width: 15%;
  height: 100%;
  vertical-align: middle;
  background: rgb(33, 150, 243);
  color: white;

  cursor: pointer;

  transition: all 0.3s ease-out 0.1s;
  -webkit-transition: all 0.3s ease-out 0.1s;
  -moz-transition: all 0.3s ease-out 0.1s;
  -o-transition: all 0.3s ease-out 0.1s;
  -ms-transition: all 0.3s ease-out 0.1s;
}

#left-button:hover,
#right-button:hover {
  color: #444444;
  opacity: 0.5;
}

#title {
  display: table-cell;
  width: 70%;
  height: 100%;
  vertical-align: middle;
  background: #444444;
  color: white;
}

#panel-content-container {
  display: inline-block;
  width: 100%;
  height: calc(100% - 60px);
  overflow: auto;
}

#panel-content-container::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
  background-color: #444444;
}

#panel-content-container::-webkit-scrollbar {
  width: 6px;
  background-color: #f5f5f5;
}

#panel-content-container::-webkit-scrollbar-thumb {
  /* background-color: gold; */
  background-color: rgb(33, 150, 243);
  border: 1px solid #000000;
}

.panel-content {
  display: inline-block;
  width: 100%;
  padding: 20px;
  font-size: 14px;
}

.panel-content-title {
  font-size: 20px;
  border-bottom: 1px solid;
}

.panel-content .tag {
  list-style: none;
  display: inline-block;
  border: 1px solid rgba(245, 0, 87, 1);
  border-image-source: initial;
  border-image-slice: initial;
  border-image-width: initial;
  border-image-outset: initial;
  border-image-repeat: initial;
  padding: 2pt 4pt;
  margin: 4px 2px;
  border-radius: 4px;
  background: rgba(245, 0, 87, 0.2);
  font-size: 10pt;
  color: rgba(245, 0, 87, 1);
}

#contact {
  width: 100%;
  height: 100%;
  z-index: 10;
  background: #444444;
}

.contact-item {
  color: white;
  background-color: rgb(70, 10, 130);
  display: -webkit-flex;
  display: flex;
  position: absolute;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 100%;
}

i {
  font-size: 25px;
}

/* Update */

body {
  width: 100vw;
  height: 100vh;
  background: #ecf0f1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.social-btns {
  height: 100%;
  text-align: center;
  padding: 50vh 0;
}

.social-btn {
  text-decoration: none !important;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 40px;
  height: 40px;
  position: relative;
  margin: 10px;
  background: #fff;
  border-radius: 4px;
  transition: -webkit-transform 0.08s linear;
  -webkit-transition: -webkit-transform 0.08s linear;
  transition: transform 0.08s linear;
  transition: transform 0.08s linear, -webkit-transform 0.08s linear;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  overflow: hidden;
}
.social-btn i {
  font-size: 20px;
  z-index: 2;
  transition: -webkit-transform 0.08s linear;
  -webkit-transition: -webkit-transform 0.08s linear;
  transition: transform 0.08s linear;
  transition: transform 0.08s linear, -webkit-transform 0.08s linear;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.social-btn:after {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  content: "";
  width: 120%;
  height: 120%;
  position: absolute;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  top: 90%;
  left: -110%;
}
.social-btn:hover {
  -webkit-transform: rotate(0);
  transform: rotate(0);
}
.social-btn:hover i {
  -webkit-transform: rotate(0);
  transform: rotate(0);
}
.social-btn:hover:after {
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  top: 0%;
  left: 0%;
}

.social-btn.facebook {
  color: #3b5998;
}
.social-btn.facebook:after {
  background: #3b5998;
}
.social-btn.facebook:hover {
  color: #fff;
}

.social-btn.instagram {
  color: #e4405f;
}
.social-btn.instagram:after {
  background: #e4405f;
}
.social-btn.instagram:hover {
  color: #fff;
}

.social-btn.linkedin {
  color: #0077b5;
}
.social-btn.linkedin:after {
  background: #0077b5;
}
.social-btn.linkedin:hover {
  color: #fff;
}

.social-btn.github {
  color: #181717;
}
.social-btn.github:after {
  background: #181717;
}
.social-btn.github:hover {
  color: #fff;
}

#maincontainer {
  position: fixed;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: black;
}

