#loading-screen {
  position: fixed;
  /* display: none; */
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 1);
  z-index: 3;
}

#cover {
  position: fixed;
  /* display: none; */
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.97);
  z-index: 2;

  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  font-size: 2vmin;
  color: #fff;
  text-align: center;
}

#cover-container {
  height: 80%;
  padding: 5% 5%;
  z-index: 3;
  overflow: visible;
  text-align: center;
}

.cover-photo img {
  text-align: center;
  height: 30vmin;
  /* border: 2px solid darkslategray; */
  border-radius: 100%;
  /* box-shadow: 2px 2px gray; */
}

.cover-title {
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 8vmin;
  letter-spacing: 0.34em;
  margin: 25px auto;

  background: -webkit-linear-gradient(white, #38495a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.cover-text {
  text-align: right;
  letter-spacing: 0.1em;
  padding-left: 25%;
}

#button-container {
  height: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#bootstrap-overrides-btn {
  border-radius: 0;
}
