#works {
  width: 100%;
  height: 100%;
  /* background: lightgray; */
}

#stage {
  display: inline-flex;
  width: 70%;
  height: 100%;
  background: #444444;
}

#panel {
  position: absolute;
  display: inline-block;
  width: 30%;
  height: 100%;
  background: lightgray;
  box-shadow: 0px 0px 20px #222222;
}

#stage-video {
  width: 100%;
}

#header {
  display: table;
  width: 100%;
  height: 60px;
  text-align: center;
  /* margin-top: 10px; */
}

#left-button,
#right-button {
  display: table-cell;
  width: 15%;
  height: 100%;
  vertical-align: middle;
  background: rgb(33, 150, 243);
  color: white;

  cursor: pointer;

  transition: all 0.3s ease-out 0.1s;
  -webkit-transition: all 0.3s ease-out 0.1s;
  -moz-transition: all 0.3s ease-out 0.1s;
  -o-transition: all 0.3s ease-out 0.1s;
  -ms-transition: all 0.3s ease-out 0.1s;
}

#left-button:hover,
#right-button:hover {
  color: #444444;
  opacity: 0.5;
}

#title {
  display: table-cell;
  width: 70%;
  height: 100%;
  vertical-align: middle;
  background: #444444;
  color: white;
}

#panel-content-container {
  display: inline-block;
  width: 100%;
  height: calc(100% - 60px);
  overflow: auto;
}

#panel-content-container::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
  background-color: #444444;
}

#panel-content-container::-webkit-scrollbar {
  width: 6px;
  background-color: #f5f5f5;
}

#panel-content-container::-webkit-scrollbar-thumb {
  /* background-color: gold; */
  background-color: rgb(33, 150, 243);
  border: 1px solid #000000;
}

.panel-content {
  display: inline-block;
  width: 100%;
  padding: 20px;
  font-size: 14px;
}

.panel-content-title {
  font-size: 20px;
  border-bottom: 1px solid;
}

.panel-content .tag {
  list-style: none;
  display: inline-block;
  border: 1px solid rgba(245, 0, 87, 1);
  border-image-source: initial;
  border-image-slice: initial;
  border-image-width: initial;
  border-image-outset: initial;
  border-image-repeat: initial;
  padding: 2pt 4pt;
  margin: 4px 2px;
  border-radius: 4px;
  background: rgba(245, 0, 87, 0.2);
  font-size: 10pt;
  color: rgba(245, 0, 87, 1);
}
