#contact {
  width: 100%;
  height: 100%;
  z-index: 10;
  background: #444444;
}

.contact-item {
  color: white;
  background-color: rgb(70, 10, 130);
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
}

i {
  font-size: 25px;
}

/* Update */

body {
  width: 100vw;
  height: 100vh;
  background: #ecf0f1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.social-btns {
  height: 100%;
  text-align: center;
  padding: 50vh 0;
}

.social-btn {
  text-decoration: none !important;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  position: relative;
  margin: 10px;
  background: #fff;
  border-radius: 4px;
  transition: -webkit-transform 0.08s linear;
  transition: transform 0.08s linear;
  transition: transform 0.08s linear, -webkit-transform 0.08s linear;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  overflow: hidden;
}
.social-btn i {
  font-size: 20px;
  z-index: 2;
  transition: -webkit-transform 0.08s linear;
  transition: transform 0.08s linear;
  transition: transform 0.08s linear, -webkit-transform 0.08s linear;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.social-btn:after {
  transition: all 0.3s;
  content: "";
  width: 120%;
  height: 120%;
  position: absolute;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  top: 90%;
  left: -110%;
}
.social-btn:hover {
  -webkit-transform: rotate(0);
  transform: rotate(0);
}
.social-btn:hover i {
  -webkit-transform: rotate(0);
  transform: rotate(0);
}
.social-btn:hover:after {
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  top: 0%;
  left: 0%;
}

.social-btn.facebook {
  color: #3b5998;
}
.social-btn.facebook:after {
  background: #3b5998;
}
.social-btn.facebook:hover {
  color: #fff;
}

.social-btn.instagram {
  color: #e4405f;
}
.social-btn.instagram:after {
  background: #e4405f;
}
.social-btn.instagram:hover {
  color: #fff;
}

.social-btn.linkedin {
  color: #0077b5;
}
.social-btn.linkedin:after {
  background: #0077b5;
}
.social-btn.linkedin:hover {
  color: #fff;
}

.social-btn.github {
  color: #181717;
}
.social-btn.github:after {
  background: #181717;
}
.social-btn.github:hover {
  color: #fff;
}
